import React from 'react';
import { Text as RNText, TextProps } from 'react-native';

export default function Text({ children, style, ...props }: TextProps) {
  return (
    <RNText
      style={[
        {
          fontFamily: 'Manrope',
        },
        style,
      ]}
      {...props}
    >
      {children}
    </RNText>
  );
}
